	module.exports = {"data":{"postsJson":{"title":"Concílio em Jerusalém","questionsTitle":"INDO MAIS FUNDO","display":"block","bookName":"Atos","ref":"15:1-15:36","html":"<p>   <strong>15</strong><u>1</u>Alguns homens foram da região da Judéia para a cidade de Antioquia e começaram a ensinar aos irmãos que eles não poderiam ser salvos se não fossem circuncidados, como manda a Lei de Moisés.<br />   <u>2</u>Paulo e Barnabé não concordaram e tiveram uma discussão muito forte com eles a respeito disso. Aí foi resolvido que Paulo e Barnabé e mais alguns irmãos fossem para Jerusalém, a fim de estudar esse assunto com os apóstolos e os presbíteros da igreja. <u>3</u>Então a igreja de Antioquia mandou que eles fossem. Eles passaram pelas regiões da Fenícia e da Samaria, contando como os não-judeus estavam se convertendo a Deus. E todos os irmãos ficaram muito alegres com essa notícia. <u>4</u>Quando chegaram a Jerusalém, foram recebidos pela igreja, pelos apóstolos e pelos presbíteros e lhes contaram tudo o que Deus havia feito por meio deles. <u>5</u>Mas alguns membros do partido dos fariseus que também haviam crido se levantaram e disseram:</p><h1>Judaizantes</h1><h4>Os não-judeus têm de ser circuncidados e têm de obedecer à Lei de Moisés.</h4><p><u>6</u>Então os apóstolos e os presbíteros se reuniram para estudar o assunto. <u>7</u>Depois de muita discussão, Pedro se levantou e disse:</p><h1>Simão Pedro</h1><h3>Meus irmãos, vocês sabem que há muito tempo Deus me escolheu entre vocês para anunciar o evangelho aos não-judeus a fim de que eles pudessem ouvir e crer. <u>8</u>E Deus, que conhece o coração de todos, mostrou que aceita os não-judeus, pois deu o Espírito Santo também a eles, assim como tinha dado a nós. <u>9</u>Deus não fez nenhuma diferença entre nós e eles; ele perdoou os pecados deles porque eles creram. <u>10</u>Então por que é que vocês estão querendo pôr Deus à prova, colocando uma carga nas costas dos que agora estão crendo? E essa carga nem nós nem os nossos antepassados pudemos carregar. <u>11</u>Pelo contrário, por meio da graça do Senhor Jesus, nós, judeus, cremos e somos salvos do mesmo modo que os não-judeus.</h3><p><u>12</u>Então todos os que estavam ali ficaram calados e escutaram Barnabé e Paulo contarem todos os milagres e maravilhas que Deus tinha feito por meio deles entre os não-judeus. <u>13</u>Quando eles terminaram de falar, Tiago disse:</p><h1>Tiago, irmão de Jesus</h1><h4>Meus irmãos, escutem! <u>14</u>Simão acabou de explicar como Deus primeiro mostrou o seu cuidado pelos não-judeus, escolhendo do meio deles um povo que seria dele. <u>15</u>As palavras dos profetas estão bem de acordo com isso, pois as Escrituras Sagradas dizem:<br />   <u>16</u>'Depois disso eu voltarei — diz o Senhor —<br />   e construirei de novo o reino de Davi,<br />   que é como uma casa que caiu.<br />   Juntarei de novo os pedaços dela<br />   e tornarei a levantá-la.<br />   <u>17</u>Assim todas as outras pessoas,<br />   todos os outros povos que eu chamei para serem meus,<br />   vão procurar conhecer o Senhor.<br />   Assim diz o Senhor,<br />   <u>18</u>que anunciou essas coisas desde os tempos antigos.'</h4><p><u>19</u>E Tiago continuou:</p><h1>Tiago, irmão de Jesus</h1><h4>A minha opinião é esta: eu acho que não devemos atrapalhar os não-judeus que estão se convertendo a Deus. <u>20</u>Penso que devemos escrever a eles uma carta, dizendo que não comam a carne de animais que foram oferecidos em sacrifício aos ídolos, que não pratiquem imoralidade sexual, que não comam a carne de nenhum animal que tenha sido estrangulado e que não comam sangue. <u>21</u>Pois, desde os tempos antigos, a Lei de Moisés tem sido lida todos os sábados nas sinagogas, e as suas palavras são anunciadas em todas as cidades.</h4><p><u>22</u>Então os apóstolos e os presbíteros, com o apoio de toda a igreja, resolveram escolher entre eles alguns homens e mandá-los para Antioquia com Paulo e Barnabé. Os escolhidos foram Judas, chamado Barsabás, e Silas. Esses dois homens eram muito respeitados pelos membros da igreja. <u>23</u>E mandaram por eles a seguinte carta:</p><h1>O Conselho de Jerusalém</h1><h4>\"Nós, os apóstolos e os presbíteros, irmãos de vocês, mandamos saudações aos nossos irmãos não-judeus que vivem em Antioquia e na província da Síria e na região da Cilícia.<br />   <u>24</u>Soubemos que alguns do nosso grupo foram até aí e disseram coisas que criaram problemas para vocês. Porém não foi com a nossa autorização que eles fizeram isso. <u>25</u>Portanto, nós todos resolvemos, sem nenhum voto contra, escolher alguns homens e mandá-los a vocês. Eles vão com os nossos queridos irmãos Barnabé e Paulo, <u>26</u>que têm arriscado a sua vida a serviço do nosso Senhor Jesus Cristo. <u>27</u>Estamos enviando, então, Judas e Silas para falarem pessoalmente com vocês sobre estas coisas que estamos escrevendo. <u>28</u>Porque o Espírito Santo e nós mesmos resolvemos não pôr nenhuma carga sobre vocês, a não ser estas proibições que são, de fato, necessárias: <u>29</u>não comam a carne de nenhum animal que tenha sido oferecido em sacrifício aos ídolos; não comam o sangue nem a carne de nenhum animal que tenha sido estrangulado; e não pratiquem imoralidade sexual. Vocês agirão muito bem se não fizerem essas coisas. Saúde a todos!</h4><p><u>30</u>Então mandaram que os quatro partissem, e eles foram para Antioquia. Lá reuniram os cristãos e entregaram a carta. <u>31</u>Quando estes a leram, ficaram muito alegres com as palavras de ânimo que havia nela. <u>32</u>Judas e Silas, que eram profetas, falaram muito com os irmãos, dando-lhes assim ânimo e força. <u>33</u>Eles passaram algum tempo ali, e depois os irmãos, fazendo votos de boa viagem, os mandaram de volta para aqueles que os tinham enviado. <u>34</u>[Porém Silas achou melhor ficar ali.]<br />   <u>35</u>Mas Paulo e Barnabé ficaram algum tempo em Antioquia. Eles e muitos outros cristãos ensinavam e anunciavam a palavra do Senhor.<br />   <u>36</u>Algum tempo depois, Paulo disse a Barnabé:</p><h1>Paulo</h1><h3>Vamos voltar e visitar os irmãos em todas as cidades onde já anunciamos a palavra do Senhor. Vamos ver se eles estão bem.</h3>","path":"/358","Question1":"Que problema fez com que os apóstolos e anciãos se reunirem em Jesuralém para debaterem?","Question2":"De acordo com Pedro nessa história, como os crentes são salvos?","Question3":"Você sente a necessidade de cumprir certas expectativas religosas para ser salvo?","Question4":"Assim como o Espírito Santo confirmou a verdade do Evangelho para os apóstolos a anciãos, o que o Espírito Santo está falando para você?","prevPath":"/357","nextPath":"/359"}},"pathContext":{}}

